'use strict';
import Common from '../../util/_common';

/**
 * @classname FormAddAnswerView
 * @classdesc 
 */
export default class FormAddAnswerView {
	/**
	 * @constructor
	 * @param {jQuery} $e -  jQuery 
	 */
	constructor($e, controller) {
		console.log('FormAddAnswerView.constructor');
		this._$e = $e;
		this._controller = controller;
		this._$answer = $e.closest('.list-item');
		$e.on('click', () => this.onClick());
	};

	/**
	 * 
	 *
	 * @memberof FormAddAnswerView
	 * @return {undefined}
	 */
	onClick() {
		console.log('FormAddAnswerView.onClick');
		let filled = true;
		this._$answer.find('.answer_text').each((idx, e) => {
			const $e = $(e);
			if($e.val() === '') filled = false;
		});
		if(filled){
			this._controller.addAnswer();
		}else{
			Common.showErrorModal('')
		}
	};
}
