/**
 * @file Form Controller
 */

'use strict';
import Listener from '../util/_listener';
import ApplyClass from '../util/_apply-class';
import FormModel from '../model/_form-model';
import FormValidateView from '../view/form/_form-validate-view';
import Template from '../template/_template';
import FormAddQuestion from '../view/form/_form-add-question-view';
import FormDeleteQuestion from '../view/form/_form-delete-question-view';
import FormAddAnswer from '../view/form/_form-add-answer-view';
import FormDeleteAnswer from '../view/form/_form-delete-answer-view';

let instance = null;

/**
 * @classname Form
 * @classdesc Form Controller
 */
export default class Form {

	/**
	 * @constructor
	 */
	constructor() {
		if (instance) {
			return instance;
		}
		console.log('Form.constructor');
		this._listener = new Listener();
		this._$form = null;
		this._submit = true;

		this._restRequiredNum = 0;

		this._prAreaNo = 1;

		// image upload
		this._model = new FormModel();
		this._imageId = null;
		this._type = null;
		this._imageNo = null;
		this._model.on('uploadMovieComplete', () => {
			this._movieData = this._model._data;
			this._listener.trigger('uploadMovieComplete');
		});
		this._model.on('uploadPouchComplete', () => {
			this._pouchData = this._model._data;
			this._listener.trigger('uploadPouchComplete');
		});
		this._model.on('uploadScriptComplete', () => {
			this._scriptData = this._model._data;
			this._listener.trigger('uploadScriptComplete');
		});
		this._model.on('uploadPdfComplete', () => {
			this._pdfData = this._model._data;
			this._listener.trigger('uploadPdfComplete');
		});
		this._model.on('getBaseComplete', () => {
			this._base = this._model._data;
			this._listener.trigger('getBaseComplete');
		});

		this._questionNum = parseInt($('.list-question').attr('data-question-num'));
		this._answerNum = null;

		instance = this;
		return instance;
	};


	/**
	 * 
	 *
	 * @memberof Form
	 * @param {jQuery} $form - 
	 * @return {undefined}
	 */
	completePreProcessing($form) {
		console.log('Form.completePreProcessing');
		this._$form = $form;
		this._listener.trigger('completePreProcessing');
	};


	/**
	 * 
	 *
	 * @memberof Form
	 * @param {jQuery} $form - 
	 * @return {undefined}
	 */
	validate($form) {
		console.log('Form.validate');
		this._$form = $form;
		this._listener.trigger('validate');
		setTimeout(() => {
			this._listener.trigger('completeValidate');
		}, 500);
	};


	/**
	 * 
	 *
	 * @memberof Form
	 * @param {jQuery} $form - 
	 * @return {undefined}
	 */
	validateModal($form) {
		console.log('Form.validate');
		this._$form = $form;
		this._listener.trigger('validate');
	};


	/**
	 * 
	 *
	 * @memberof Form
	 * @param {jQuery} $form - 
	 * @return {undefined}
	 */
	completePostProcessing($form) {
		console.log('Form.completePostProcessing');
		this._$form = $form;
		this._listener.trigger('completePostProcessing');
	};


	/**
	 * 
	 *
	 * @memberof Form
	 * @param {blob} file - 
	 * @param {string} type -  1: 2: 3:
	 * @return {undefined}
	 */
	uploadFile(file, type) {
		console.log('Form.uploadFile');
		this._type = type;
		this._model.uploadFile(file, type);
	};


	/**
	 * 
	 *
	 * @memberof Form
	 * @param {string} imageId - ID
	 * @return {undefined}
	 */
	deleteFile(fileId) {
		console.log(`Form.deleteImage imageId:${fileId}`);
		this._fileId = fileId;
		this._model.deleteFile(fileId);
	};


	/**
	 * 
	 *
	 * @memberof Form
	 * @param {number} type - 
	 * @return {undefined}
	 */
	getBase(type) {
		console.log('Form.getBase');
		this._model.getBase(type);
	};


	/**
	 * 
	 *
	 * @memberof Form
	 * @return {undefined}
	 */
	addQuestion() {
		console.log('Form.addQuestion');
		$('.list-question .list-item .btn').hide();
		this._questionNum ++;
		const $listQuestion = $('.list-question');
		$listQuestion.attr('data-question-num', this._questionNum);
		const additionalQuestion = Template.addQuestion(this._questionNum);
		$listQuestion.append(additionalQuestion);
		const $lastQuestion = $('.list-question .list-item:last-child');
		$lastQuestion.find('.validate').each((idx, e) => {
			const $e = $(e);
			ApplyClass.apply(FormValidateView, $e, [$e, this]);
		});
		const $addQuestion = $lastQuestion.find('.btn-add-question');
		const $deleteQuestion = $lastQuestion.find('.btn-delete-question');
		const $addAnswer = $lastQuestion.find('.btn-add-answer');
		ApplyClass.apply(FormDeleteQuestion, $deleteQuestion, [$deleteQuestion, this]);
		ApplyClass.apply(FormAddAnswer, $addAnswer, [$addAnswer, this]);
		if(this._questionNum >= window.const.test.MAX_QUESTION_NUM){
			$addQuestion.hide();
		}else{
			ApplyClass.apply(FormAddQuestion, $addQuestion, [$addQuestion, this]);
		}
		console.log(`answerNum is ${this._answerNum}`);
	};


	/**
	 * 
	 *
	 * @memberof Form
	 * @return {undefined}
	 */
	addAnswer() {
		console.log('Form.addAnswer');
		$('.list-answers .list-item .btn').hide();
		const $lastQuestion = $('.list-question .list-item:last-child');
		this._answerNum = parseInt($lastQuestion.find('.list-answers').attr('data-answer-num'));
		this._answerNum ++;
		$('.list-answers').attr('data-answer-num', this._answerNum);
		const additionalAnswer = Template.addAnswer(this._questionNum, this._answerNum);
		$('.list-question > .list-item:last-child .list-answers').append(additionalAnswer);
		const $lastAnswer = $('.list-answers .list-item:last-child');
		$lastAnswer.find('.validate').each((idx, e) => {
			const $e = $(e);
			ApplyClass.apply(FormValidateView, $e, [$e, this]);
		});
		const $addAnswer = $lastAnswer.find('.btn-add-answer');
		const $deleteAnswer = $lastAnswer.find('.btn-delete-answer');
		ApplyClass.apply(FormDeleteAnswer, $deleteAnswer, [$deleteAnswer, this]);
		if(this._answerNum >= window.const.test.MAX_ANSWER_NUM){
			$addAnswer.hide();
		}else{
			ApplyClass.apply(FormAddAnswer, $addAnswer, [$addAnswer, this]);
		}
	};


	/**
	 * listener on
	 *
	 * @memberof Form
	 * @param {string} name - 
	 * @param {function} fn - 
	 * @return {undefined}
	 */
	on(name, fn) {
		this._listener.on(name, fn);
	};


	/**
	 * listener off
	 *
	 * @memberof Form
	 * @param {string} name - 
	 * @param {function} fn - 
	 * @return {undefined}
	 */
	off(name, fn) {
		this._listener.off(name, fn);
	};
}
