/**
 * @file GetBaseView
 *
 */
'use strict';
import ApplyClass from '../../util/_apply-class';
import FormSelect2View from './_form-select2-view';
import FormValidateView from './_form-validate-view';

/**
 * @classname GetBaseView
 * @classdesc GetBaseView
 */
export default class GetBaseView {
	/**
	 * @constructor
	 * @param {jQuery} $e -  jQuery 
	 * @param {object} controller - controller
	 */
	constructor($e, controller) {
		console.log('GetBaseView.constructor');
		this._$e = $e;
		this._controller = controller;
		$e.on('change', () => this.onChange());
		controller.on('getBaseComplete', () => this.setBase());
	};


	/**
	 * 
	 *
	 * @memberof GetBaseView
	 * @return {undefined}
	 */
	onChange() {
		console.log(`GetBaseView.onChange`);
		const organizationId = parseInt(this._$e.val());
		if(this._$e.hasClass('type')){
			if(organizationId === 1){
				this.getBase(1);
			}else if(organizationId === 2){
				this.getBase(2);
			}else{
				this.clearBase();
			}
		}else{
			if(organizationId === 5 || organizationId === 7){
				this.getBase(1);
			}else if(organizationId === 6 || organizationId === 8){
				this.getBase(2);
			}else{
				this.clearBase();
			}
		}
	};


	/**
	 * 
	 *
	 * @memberof GetBaseView
	 * @return {undefined}
	 */
	getBase(type) {
		console.log(`GetBaseView.getBase`);
		this._controller.getBase(type);
	};


	/**
	 * 
	 *
	 * @memberof GetBaseView
	 * @return {undefined}
	 */
	setBase() {
		console.log(`GetBaseView.setBase`);
		const base = this._controller._base.base;
		let options = '<option value=""></option>';
		if(this._$e.hasClass('type')) options = '<option value=""></option>';
		for(let i = 0; i < base.length; i++){
			options += `<option value="${base[i].base_id}">${base[i].base_name}</option>`
		}
		let $select = '<select class="form-control validate select2 base_id" id="base_id" name="base_id" data-validate="empty"></select>';
		if(this._$e.attr('data-search-type') === 'select'){
			$select = '<select class="form-control validate select2 base_id" id="base_id" name="base_id" data-search-item="base_id" data-search-type="select"></select>';
		}
		$('.base-select-wrap').html($select);
		const $baseId = $('#base_id');
		$baseId.html(options);
		const FormController = this._controller;
		ApplyClass.apply(FormValidateView, $baseId, [$baseId, FormController]);
		ApplyClass.apply(FormSelect2View, $baseId, [$baseId, FormController]);
	};


	/**
	 * 
	 *
	 * @memberof GetBaseView
	 * @return {undefined}
	 */
	clearBase() {
		console.log(`GetBaseView.clearBase`);
		$('.base-select-wrap').html('');
	};
}
