/**
 * @file FormModel Controller
 */

'use strict';
import Listener from '../util/_listener';
import Common from '../util/_common';

let instance = null;

/**
 * @classname FormModel
 * @classdesc Form Model
 */
export default class FormModel {

	/**
	 * @constructor
	 */
	constructor() {
		if (instance) {
			return instance;
		}

		this._listener = new Listener();
		this._jqXHR = null;
		this._post = false;
		this._data = null;
		this._message = null;
		this._code = null;
		// csrf add
		$.ajaxSetup({
			headers: {
				'X-CSRF-TOKEN': $('meta[name="csrf-token"]').attr('content')
			}
		});

		instance = this;
		return instance;
	};


	/**
	 *
	 * 
	 * @memberof FormModel
	 * @param {blob} file - 
	 * @param {string} type -  movie: pdf:PDF
	 * @return {undefined}
	 */
	uploadFile(file, type) {
		if(this._post) return;
		console.log(`FormModel.uploadFile/ type: ${type}`);
		console.log(file);
		Common.showLoading();
		this._post = true;
		this._type = type;
		let formData = new FormData();
		let url;
		formData.append('type', type);
		if(type === 1){
			console.log('');
			url = window.const.API_URL.UPLOAD_MOVIE;
			formData.append('video_file', file);
		}else if(type === 2){
			console.log('');
			url = window.const.API_URL.UPLOAD_POUCH;
			formData.append('pouch_file', file);
		}else if(type === 3){
			console.log('');
			url = window.const.API_URL.UPLOAD_SCRIPT;
			formData.append('script_file', file);
		}else if(type === 4){
			console.log('VISION');
			url = window.const.API_URL.UPLOAD_PDF;
			formData.append('pdf_file', file);
		}
		console.log(url);
		let params = {};
		params['data'] = formData;
		params['method'] = 'POST';
		params['dataType'] = 'json';
		params['url'] = url;
		params['processData'] = false;
		params['contentType'] = false;
		console.log(JSON.stringify(params));
		this._jqXHR = $.ajax(params).always(this.onUploadFileComplete.bind(this));
	};


	/**
	 * ajax
	 *
	 * @memberof FormModel
	 * @param {Object} data - data|jqXHR
	 * @param {String} textStatus - "success", "notmodified", "nocontent", "error", "timeout", "abort", or "parsererror"
	 * @return {undefined}
	 */
	onUploadFileComplete(data, textStatus) {
		console.log('FormModel.onUploadFileComplete');
		Common.hideLoading();
		this._data = data.result;
		this._code = data.code;
		this._message = data.message;
		console.log(`code: ${this._code} / message: ${this._message}`);
		if(textStatus === 'success'){
			if(this._code !== 0){
				this._post = false;
				Common.showErrorModal(`${this._message}<br>${this._code}`);
			}else{
				this._post = false;
				if(this._type === 1){
					this._listener.trigger('uploadMovieComplete');
				}else if(this._type === 2){
					this._listener.trigger('uploadPouchComplete');
				}else if(this._type === 3){
					this._listener.trigger('uploadScriptComplete');
				}else if(this._type === 4){
					this._listener.trigger('uploadPdfComplete');
				}
			}
		}else{
			this._post = false;
			this._code = 9999;
			this._message = '<br>';
			Common.showErrorModal(`${this._message}<br>${this._code}`);
			console.log(`ajax error (onUploadImageComplete): ${textStatus}`);
		}
		this._jqXHR = null;
	};


	/**
	 *
	 * 
	 * @memberof FormModel
	 * @param {string} fileId - ID
	 * @return {undefined}
	 */
	deleteFile(fileId) {
		if(this._post) return;
		console.log(`FormModel.deleteFile`);
		Common.showLoading();
		this._post = true;
		let formData = {
			file_id: fileId
		};
		const url = window.const.API_URL.DELETE_FILE;
		let params = {};
		params['data'] = formData;
		params['method'] = 'POST';
		params['dataType'] = 'json';
		params['url'] = url;
		console.log(JSON.stringify(params));
		this._jqXHR = $.ajax(params).always(this.onDeleteImageComplete.bind(this));
	};


	/**
	 * ajax
	 *
	 * @memberof FormModel
	 * @param {Object} data - data|jqXHR
	 * @param {String} textStatus - "success", "notmodified", "nocontent", "error", "timeout", "abort", or "parsererror"
	 * @return {undefined}
	 */
	onDeleteImageComplete(data, textStatus) {
		console.log('FormModel.onDeleteImageComplete');
		Common.hideLoading();
		this._data = data.result;
		this._code = data.code;
		this._message = data.message;
		console.log(`code: ${this._code} / message: ${this._message}`);
		if(textStatus === 'success'){
			if(this._code !== 0){
				this._post = false;
				Common.showErrorModal(`${this._message}<br>${this._code}`);
			}else{
				this._post = false;
				this._listener.trigger('deleteImageComplete');
			}
		}else{
			this._post = false;
			this._code = 9999;
			this._message = '<br>';
			Common.showErrorModal(`${this._message}<br>${this._code}`);
			console.log(`ajax error (onDeleteImageComplete): ${textStatus}`);
		}
		this._jqXHR = null;
	};


	/**
	 *
	 * 
	 * @memberof FormModel
	 * @param {number} type - 
	 * @return {undefined}
	 */
	getBase(type) {
		if(this._post) return;
		console.log(`FormModel.getBase / ${type}`);
		Common.showLoading();
		this._post = true;
		let formData = new FormData();
		formData.append('type', type);
		const url = window.const.API_URL.GET_BASE;
		let params = {};
		params['data'] = formData;
		params['method'] = 'POST';
		params['dataType'] = 'json';
		params['url'] = url;
		params['processData'] = false;
		params['contentType'] = false;
		console.log(JSON.stringify(params));
		this._jqXHR = $.ajax(params).always(this.onGetBaseComplete.bind(this));
	};


	/**
	 * ajax
	 *
	 * @memberof FormModel
	 * @param {Object} data - data|jqXHR
	 * @param {String} textStatus - "success", "notmodified", "nocontent", "error", "timeout", "abort", or "parsererror"
	 * @return {undefined}
	 */
	onGetBaseComplete(data, textStatus) {
		console.log('FormModel.onGetBaseComplete');
		Common.hideLoading();
		this._data = data.result;
		this._code = data.code;
		this._message = data.message;
		console.log(`code: ${this._code} / message: ${this._message}`);
		if(textStatus === 'success'){
			if(this._code !== 0){
				this._post = false;
				Common.showErrorModal(`${this._message}<br>${this._code}`);
			}else{
				this._post = false;
				this._listener.trigger('getBaseComplete');
			}
		}else{
			this._post = false;
			this._code = 9999;
			this._message = '<br>';
			Common.showErrorModal(`${this._message}<br>${this._code}`);
			console.log(`ajax error (onGetBaseComplete): ${textStatus}`);
		}
		this._jqXHR = null;
	};


	/**
	 * listener on
	 *
	 * @memberof FormModel
	 * @param {string} name - 
	 * @param {function} fn - 
	 * @return {undefined}
	 */
	on(name, fn) {
		this._listener.on(name, fn);
	};


	/**
	 * listner off
	 *
	 * @memberof FormModel
	 * @param {string} name - 
	 * @param {function} fn - 
	 * @return {undefined}
	 */
	off(name, fn) {
		this._listener.off(name, fn);
	};
}
