/**
 * @file 
 */
'use strict';

import ApplyClass from '../../util/_apply-class';
import Common from '../../util/_common';
import FormValidateView from './_form-validate-view';
import Template from '../../template/_template';

/**
 * @classname FormFileDropView
 * @classdesc 
 */
export default class FormFileDropView {
	/**
	 * @constructor
	 * @param {object} controller - 
	 * @param {jQuery} $e -  jQuery 
	 */
	constructor($e, controller) {
		console.log('FormFileDropView.constructor');
		this._$e = $e;
		this._controller = controller;
		this._template = new Template();
		this._$blockFileUpload = $e.closest('.block-file-upload');
		this._$inputFile = this._$blockFileUpload.find('input');
		this._$inputFileWrap = this._$blockFileUpload.find('.block-file-input');
		if(this._$e.hasClass('file-upload-movie')){
			this._inputFile = `
			<input class="file-validate" type="file" accept="video/mp4" name="movie_path" style="display: none;">
			<input id="movieID" type="hidden" name="movie_path" value="bbbbb">`;
		}else{
			this._inputFile = '<input class="file-validate" type="file" accept="application/pdf, image/*, video/mp4" style="display: none;">';
		}
		this._type = parseInt($e.attr('data-type'));
		this._fileType = null;
		this._orientation = 1;
		this._$photoArea = $e.closest('.form-group').find('.block-photo-area');
		this._$modal = $('.modal-movie');
		this._$e.on('dragover', e => this.onDragover(e));
		this._$e.on('dragleave', () => this.onDragleave());
		this._$e.on('drop', e => this.onDrop(e));
		this._$e.on('click', e => this.onClick(e));
		this._controller.on('uploadMovieComplete', () => {
			this.onUploadMovieComplete();
		});
		this._controller.on('uploadPouchComplete', () => {
			this.onUploadPouchComplete();
		});
		this._controller.on('uploadScriptComplete', () => {
			this.onUploadScriptComplete();
		});
		this._controller.on('uploadPdfComplete', () => {
			this.onUploadPdfComplete();
		});
		this._$inputFile.on('change', e => this.onChange(e));
		this._$modal.on('hidden.bs.modal', () => {
			this._$modal.find('.block-video-area').html('');
			this._$modal.find('span.text-title').text('');
		})
	};


	/**
	 * 
	 *
	 * @memberof FormFileDropView
	 * @return {undefined}
	 */
	onClick(e){
		console.log('FormFileDropView.onClick');
		e.preventDefault();
		e.stopPropagation();
		console.log('inputFile:');
		console.log(this._$inputFile);
		this._$inputFile.click();
	}


	/**
	 * 
	 *
	 * @memberof FormFileDropView
	 * @param {object} e - 
	 * @return {undefined}
	 */
	onDragover(e) {
		e = e.originalEvent;
		e.dataTransfer.dropEffect = 'copy';
		e.preventDefault();
		this._$e.addClass('dragover');
	};


	/**
	 * 
	 *
	 * @memberof FormFileDropView
	 * @return {undefined}
	 */
	onDragleave() {
		this._$e.removeClass('dragover');
	};


	/**
	 * 
	 *
	 * @memberof FormFileDropView
	 * @return {undefined}
	 */
	onDrop(e) {
		console.log('FormFileDropView.onDrop');
		e = e.originalEvent;
		e.preventDefault();
		this._$e.removeClass('dragover');
		this._files = e.dataTransfer.files;
		if(this._files.length > 1) {
			Common.showErrorModal('1');
			return;
		}
		const file = this._files[0];
		const maxMB = window.const.file.MAX_MB;
		const maxSize = maxMB * 1024 * 1024;
		if (file.size > maxSize) {
			Common.showErrorModal(`${maxMB}MB`);
			return;
		}
		this._fileName = file.name;
		const mime = file.type;
		if(this._$e.hasClass('file-upload-credo')) {
			console.log(mime);
			if(mime.match(/^video\/mp4/) || mime.match(/^image/) || mime.match(/application\/pdf/)){
			}else{
				Common.showErrorModal(`PDF`);
				return;
			}
			if(mime.match(/^video\/mp4/)){
				this._fileType = 'video';
			}else{
				this._fileType = 'pdf';
			}
		}else if(this._$e.hasClass('file-upload-pdf')){
			console.log(mime);
			if(mime.match(/^image/) || mime.match(/application\/pdf/)){
			}else{
				Common.showErrorModal(`PDF`);
				return;
			}
		}
		this._controller.uploadFile(file, this._type);
	};


	/**
	 * 
	 *
	 * @memberof FormFileDropView
	 * @return {undefined}
	 */
	onChange(e) {
		console.log('FormFileDropView.onChange');
		const file = e.target.files[0];
		const maxMB = window.const.file.MAX_MB;
		const maxSize = maxMB * 1024 * 1024;
		if (file.size > maxSize) {
			Common.showErrorModal(`${maxMB}MB`);
			return;
		}
		const mime = file.type;
		if(mime.match(/^video\/mp4/)){
			this._fileType = 'video';
		}else{
			this._fileType = 'pdf';
		}
		this._fileName = file.name;
		const reader = new FileReader();
		reader.onload = this._controller.uploadFile(file, this._type);
	};


	/**
	 * 
	 *
	 * @memberof FormFileDropView
	 * @return {undefined}
	 */
	onUploadMovieComplete() {
		console.log('FormFileDropView.onUploadMovieComplete');
		const movieData = this._controller._movieData;
		const movie = `<div class="upload-movie-wrap"><p class="text small"></p><p class="text small">${this._fileName}</p><button type="button" class="btn btn-main btn-movie btn-sm mt10" data-url="${movieData.url}" data-toggle="modal" data-target=".modal-movie"></button><input type="hidden" name="movie_path" value="${movieData.path}"></div>`;
		this._$photoArea.html(movie);
		this._$e.closest('.form-group').find('.error-tip').removeClass('show').find('.error-tip-inner').text('');
		this._$inputFileWrap.html(this._inputFile);
		this._$inputFile = this._$blockFileUpload.find('input');
		this._$inputFile.on('change', e => this.onChange(e));
		$('.btn-movie').on('click', (e) => this.watchMovie(e));
		$('.block-file-input #movieID').attr('value', movieData.path);
	};


	/**
	 * 
	 *
	 * @memberof FormFileDropView
	 * @return {undefined}
	 */
	onUploadPouchComplete() {
		console.log('FormFileDropView.onUploadPouchComplete');
		const pouchData = this._controller._pouchData;
		const pouch = `<div class="upload-pdf-wrap"><p class="text small"></p><p class="text small"><a href="${pouchData.url}" target="_blank">${this._fileName}</a></p><input type="hidden" name="pouch_path" value="${pouchData.path}"></div>`;
		this._$photoArea.html(pouch);
		this._$e.closest('.form-group').find('.error-tip').removeClass('show').find('.error-tip-inner').text('');
		this._$inputFileWrap.html(this._inputFile);
		this._$inputFile = this._$blockFileUpload.find('input');
		this._$inputFile.on('change', e => this.onChange(e));
	};


	/**
	 * 
	 *
	 * @memberof FormFileDropView
	 * @return {undefined}
	 */
	onUploadScriptComplete() {
		console.log('FormFileDropView.onUploadScriptComplete');
		const scriptData = this._controller._scriptData;
		const script = `<div class="upload-pdf-wrap"><p class="text small"></p><p class="text small"><a href="${scriptData.url}" target="_blank">${this._fileName}</a></p><input type="hidden" name="script_path" value="${scriptData.path}"></div>`;
		this._$photoArea.html(script);
		this._$e.closest('.form-group').find('.error-tip').removeClass('show').find('.error-tip-inner').text('');
		this._$inputFileWrap.html(this._inputFile);
		this._$inputFile = this._$blockFileUpload.find('input');
		this._$inputFile.on('change', e => this.onChange(e));
	};


	/**
	 * VISION
	 *
	 * @memberof FormFileDropView
	 * @return {undefined}
	 */
	onUploadPdfComplete() {
		console.log('FormFileDropView.onUploadPdfComplete');
		const pdfData = this._controller._pdfData;
		let pdf = `<div class="upload-pdf-wrap"><p class="text small"></p><p class="text small"><a href="${pdfData.url}" target="_blank">${this._fileName}</a></p><input type="hidden" name="pdf_path" value="${pdfData.path}"></div>`;
		if(this._fileType === 'video') pdf = `<div class="upload-pdf-wrap"><p class="text small"></p><p class="text small">${this._fileName}</p><button type="button" class="btn btn-main btn-movie btn-sm mt10" data-url="${pdfData.url}" data-toggle="modal" data-target=".modal-movie"></button><input type="hidden" name="pdf_path" value="${pdfData.path}"></div>`;
		this._$photoArea.html(pdf);
		this._$e.closest('.form-group').find('.error-tip').removeClass('show').find('.error-tip-inner').text('');
		this._$inputFileWrap.html(this._inputFile);
		this._$inputFile = this._$blockFileUpload.find('input');
		this._$inputFile.on('change', e => this.onChange(e));
		$('.btn-movie').on('click', (e) => this.watchMovie(e));
	};


	/**
	 * 
	 *
	 * @memberof FormFileDropView
	 * @return {undefined}
	 */
	watchMovie(e) {
		console.log('FormFileDropView.watchMovie');
		const $e = $(e.currentTarget);
		const url = $e.attr('data-url');
		const video = `
<video controls autoplay class="movie-player">
	<source src="${url}">
	<p>video</p>
</video>`;
		this._$modal.find('.block-video-area').html(video);
		this._$modal.find('span.text-title').text(this._name);
	};
}
