/**
 * @file _template.js
 */
'use strict';
import Common from '../util/_common';

/**
 * @classname Template
 */

export default class Template {


	/**
	 * 
	 *
	 * @memberof Template
	 * @param {array} image - 
	 * @return {string} - 
	 */
	static getImageList(image) {
		console.log('Template.getImageList');
		return `
<li class="list-item tac">
	<div class="image-box" style="background: #f2f2f2 url(${image.url}) no-repeat 50% 50% / contain"></div>
	<button class="btn btn-primary btn-sm btn-select-image mt10" type="button" data-image-id="${image.image_id}" data-image-url="${image.url}"><i class="fas fa-check mr10"></i></button>
</li>
`;
	};


	/**
	 * 
	 *
	 * @memberof Template
	 * @param {number} questionNo - 
	 * @return {string} - 
	 */
	static addQuestion(questionNo) {
		console.log(`Template.addQuestion / ${questionNo}`);
		let additionalQuestion = `
<li class="list-item">
	<div class="form-group validateGroup row"><label class="col-sm-3 col-form-label">${questionNo}<span class="badge badge-danger ml-2"></span></label>
		<div class="col-sm-8">
			<div class="error-tip">
				<div class="error-tip-inner"></div>
			</div>
			<textarea class="form-control validate question_text" name="question_text[${questionNo}]" rows="4" data-validate="empty max-200" maxlength="200"></textarea>
		</div>
	</div>
	<div class="form-group validateGroup row"><label class="col-sm-3 col-form-label"><span class="badge badge-danger ml-1"></span></label>
		<div class="col-sm-8">
			<ul class="list list-answers" data-answer-num="2">
				<li class="list-item"><input class="form-control validate answer_text" type="text" name="answer_text[${questionNo}][1]" data-validate="empty max-50" maxlength="50">
					<div class="custom-control custom-radio mb0"><input class="custom-control-input" id="answer${questionNo}-1" type="radio" name="correct[${questionNo}]" value="1" checked=""><label class="custom-control-label" for="answer${questionNo}-1"></label></div>
				</li>
				<li class="list-item"><input class="form-control validate answer_text" type="text" name="answer_text[${questionNo}][2]" data-validate="empty max-50" maxlength="50">
					<div class="custom-control custom-radio mb0"><input class="custom-control-input" id="answer${questionNo}-2" type="radio" name="correct[${questionNo}]" value="2"><label class="custom-control-label" for="answer${questionNo}-2"></label></div><button class="btn btn-add-answer btn-success" type="button"><i class="fas fa-plus"></i></button>
				</li>
			</ul>
		</div>
	</div>
	<button class="btn btn-add-question btn-success" type="button"><i class="fas fa-plus"></i></button>
	<button class="btn btn-delete-question btn-danger" type="button"><i class="fas fa-minus"></i></button>
</li>
`;
		return additionalQuestion;
	};


	/**
	 * 
	 *
	 * @memberof Template
	 * @param {number} questionNo - 
	 * @return {string} - 
	 */
	static addAnswer(questionNo, answerNo) {
		console.log(`Template.addAnswer / ${questionNo}-${answerNo}`);
		let additionalAnswer = `
<li class="list-item">
	<input class="form-control validate answer_text" type="text" name="answer_text[${questionNo}][${answerNo}]" data-validate="empty max-50" maxlength="50">
	<div class="custom-control custom-radio mb0">
		<input class="custom-control-input" id="answer_text${questionNo}-${answerNo}" type="radio" name="correct[${questionNo}]" value="${answerNo}">
		<label class="custom-control-label" for="answer_text${questionNo}-${answerNo}"></label>
	</div>
	<button class="btn btn-add-answer btn-success" type="button"><i class="fas fa-plus"></i></button>
	<button class="btn btn-delete-answer btn-danger" type="button"><i class="fas fa-minus"></i></button>
</li>
`;
		return additionalAnswer;
	};
}