/**
 * @file Basic Controller
 */

'use strict';
import Listener from '../util/_listener';
import ApplyClass from '../util/_apply-class';
import BasicModel from '../model/_basic-model';
let instance = null;

/**
 * @classname Basic
 * @classdesc Basic Controller
 */
export default class Basic {

	/**
	 * @constructor
	 */
	constructor() {
		if (instance) {
			return instance;
		}
		console.log('Basic.constructor');
		this._model = new BasicModel();
		this._listener = new Listener();

		instance = this;
		return instance;
	};


	/**
	 * listener on
	 *
	 * @memberof Basic
	 * @param {string} name - 
	 * @param {function} fn - 
	 * @return {undefined}
	 */
	on(name, fn) {
		this._listener.on(name, fn);
	};


	/**
	 * listener off
	 *
	 * @memberof Basic
	 * @param {string} name - 
	 * @param {function} fn - 
	 * @return {undefined}
	 */
	off(name, fn) {
		this._listener.off(name, fn);
	};
}
