	/**
	 * 
	 *
	 * @memberof Common
	 * @param {int} num - 
	 * @param {int} length - 
	 * @return {string} - 
	 */
	static zeroPadding(num, length) {
		return ('0000000000' + num).slice(-length);
	};


	/**
	 * 
	 *
	 * @memberof Common
	 * @param {string} num - 3
	 * @return {string} - 
	 */
	static separatedByComma(num) {
		return Number(num).toLocaleString();
	};


	/**
	 * 
	 *
	 * @memberof Common
	 * @param {string} price - 
	 * @return {string} - 
	 */
	static getPrice(price) {
		return parseInt(price.replace(/^/, '').replace(/,/ig, ''));
	};


	/**
	 * 
	 *
	 * @memberof Common
	 * @param {date} date - 
	 * @param {string} divider - 
	 * @param {boolean} showSec - 
	 * @param {boolean} padding - 
	 * @return {string} date - 
	 */
	static getCurrentDateTime(date, divider = '-', showSec = true, padding = false) {
		date = date ? date : new Date();
		const year 	= date.getFullYear();
		const month	= date.getMonth() + 1;
		const day 	= date.getDate();
		const hour 	= date.getHours();
		const min	= date.getMinutes();
		const sec	= date.getSeconds();
		let dateStr = '';
		if(padding){
			dateStr =  year + divider + this.zeroPadding(month, 2) + divider + this.zeroPadding(day,2) + ' ' + this.zeroPadding(hour, 2) + ':' + this.zeroPadding(min, 2);
			if(showSec) dateStr +=  ':' + this.zeroPadding(sec, 2)
		}else{
			dateStr =  year + divider + month + divider + day + ' ' + hour + ':' + min;
			if(showSec) dateStr += ':' + sec;
		}
		return dateStr;
	};



	/**
	 * 
	 *
	 * @memberof Common
	 * @param {boolean} padding - 
	 * @param {string} divider - 
	 * @return {string} date - 
	 */
	static getCurrentDate(padding = false, divider = '-') {
		let date = new Date();
		if(padding){
			return date.getFullYear() + divider + this.zeroPadding(date.getMonth() + 1, 2) + divider + this.zeroPadding(date.getDate(),2);
		}else{
			return date.getFullYear() + divider + (date.getMonth() + 1) + divider + date.getDate();
		}
	};



	/**
	 * URL
	 *
	 * @memberof Common
	 * @return {string} url - URL
	 */
	static getCurrentURL() {
		let url = document.location.href;

		if (url.match(/(.+)\?/)) {
			const match = url.match(/(.+)\?/);
			url = match[1];
		} else if (url.match(/(.+)#/)) {
			const match = url.match(/(.+)#/);
			url = match[1];
		}
		return url;
	};


	/**
	 * URL
	 *
	 * @memberof Common
	 * @return {array} param - URL key value
	 */
	static getParam() {
		let url = document.location.href;
		if (url.match(/(.+)#/)) {
			const match1 = url.match(/(.+)#/);
			url = match1[1];
		}
		if (url.match(/\?(.+)$/)) {
			const match2 = url.match(/\?(.+)$/)
			const paramString = match2[1];
			let tmp = paramString.split('&');
			let param = {};
			let tmp2, key, val;
			for (let i = 0; i < tmp.length; i++) {
				// initialize
				tmp2 = [];
				key = '';
				val = '';

				tmp2 = tmp[i].split('=');
				key = tmp2[0];
				val = tmp2[1];
				param[key] = val;
			}
			return param;
		} else {
			return {};
		}
	};


	/**
	 * 
	 *
	 * @memberof Common
	 * @return {string} url - URL
	 */
	static getParamAll() {
		let url = document.location.href;
		let param = '';
		if (url.match(/\?(.+)/)) {
			param = $1;
		}
		return param;
	};


