/**
 * @file Observer / Listener 
 */
'use strict';

/**
 * @classname Listener
 * @classdesc Observer / Listner on off trigger 
 */
export default class Listener {

	constructor(){
		this._functions = {};
	};

	/**
	 * on
	 *
	 * @memberof Listener
	 * @param {string} name - 
	 * @param {function} fn - 
	 * @return {object} - Listener 
	 */
	on(name, fn){
		this._functions[name] = this._functions[name] || [];
		this._functions[name].push(fn);

		return this;
	}


	/**
	 * off
	 *
	 * @memberof Listener
	 * @param {string} name - 
	 * @param {function} fn - 
	 * @return {object} - Listener 
	 */
	off(name, fn){
		let functions = this._functions[name];
		if (!functions) {
			return this;
		}
		for (let i = 0, l = functions.length; i < l; i++) {
			let _fn = functions[i];
			if (fn === _fn) {
				functions.splice(i, 1);
				break;
			}
		}
		return this;
	}


	/**
	 * Trigger
	 * @param {string} name - 
	 * @return {object} - Listener 
	 */
	trigger(name) {
		console.log('trigger: ' + name);
		let functions = this._functions[name] || [];
		for (let i = 0, l = functions.length; i < l; ++i) {
			let fn = functions[i];
			if (fn) {
				fn.apply(this, arguments);
			}
		}
		return this;
	}
}
